.modalForm {
  margin: 0 auto;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  border-radius: 15px;
  justify-content: center;
  background-color: rgb(255, 255, 255);
  animation-name: slide;
  animation-duration: 1s;
  padding: 25px 0;
}

.settingsHeaders {
  text-align: center;
}

.divPatternSettings {
  display: flex;
  justify-content: center;
}
.patternSettings {
  display: flex;
  align-items: center;
  justify-content: center;
}



