.header {
    padding: 20px;
}

.timerBlock {
    border-radius: 15px;
    border: 1px solid black;
    padding: 15px;
    margin-bottom: 20px;
}

.timer {
    text-align: center;
    font-size: 60px;
}

.textWork {
    text-align: center;
    font-size: 20px;
}

.textWorkh3 {
    font-weight: 400;
}

.buttons {
    display: flex;
    justify-content: center;
}

.start {
    font-size: 20px;
    margin-right: 20px;
    width: 150px;
}

.stop {
    font-size: 20px;
    width: 150px;
}

.toDoListHeader {
    text-align: center;
}

.input {
    width: 80vh;
    font-size: 18px;
    padding: 0.5rem 1rem;
}

.input[aria-invalid] {
    border-color: red;
}

.buttonPlus {
    width: 5vh;
    padding: 0.5rem 1rem;
}

.form {
    display: flex;
    align-items: flex-end;
}

.buttonSettings {
    display: flex;
    justify-content: space-between;
}
